.about-wrapper {
    background-color: #e9e9e9;
    display: flex;
    justify-content: center;
    width: 100%;
    height: auto;
    flex-wrap: wrap;
}

.about {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
    width: 1110px;
    padding: 120px 0 100px 0;
    &-text {
        padding: 55px 65px;
        border-radius: 10px;
        height: fit-content;
        box-shadow: 2px 3.464px 18px rgba(1, 1, 1, 0.14);
        font: normal 15px "Roboto";
        color: #3e3e3e;
        width: 50%;
        &-scaled {
            font: bold 30px "Arvo";
        }
    }
    .slick{
        max-width: 480px;
        @media (max-width: 767px){
            max-width: 90%;
        }
    }
    &-slider {
        font: normal 15px "Poppins";
        margin: 3px 0 0 0;
        color: #3e3e3e;
        width: 40%;
        &-header {
            font: bold 20px "Arvo";
            margin-bottom: 40px;
            @media (max-width: 767px){
                margin-bottom: 25px;
            }
        }
        @media (max-width: 640px){
            padding-left: 5%;
            padding-right: 5%;
        }
    }
    @media (max-width: 768px){
        flex-direction: column;
        align-items: center;
        padding: 25px 0 25px 0;
        &-text{
            padding: 5%;
            width: 90%;
            margin-bottom: 25px;
        }
    }
}